<template>
    <v-timeline>
        <v-timeline-item
        v-for="(timeline, i) in timeline"
        :key="i"
        color="yellow"
        small
        >
        <div class="py-4">
            <h2 :class="`headline font-weight-light mb-4 ${timeline.color}--text`">{{timeline.date}}</h2>
            <div>
                {{timeline.text}}
            </div>
        </div>
        </v-timeline-item>
  </v-timeline>
</template>

<script>
  import axios from "axios";

  export default {
    name: 'timeline',
    props: ['character', 'episode'],
    data: function () {
      return {
      };
    },
    methods: {
      say: function (){
        console.log(this.character.birthday);
      }
    },
     mounted() {
      this.$store.commit('SET_CURRENT_VIEW', 'timeline');
      this.character ? this.$store.commit('ADD_TIMELINE', {date: this.character.birthday, text: "Birthday"}) : null;
      this.episode.map(item => {this.$store.commit('ADD_TIMELINE', 
          {date: item.air_date, text: `${item.season} / ${item.episode}`})});
      this.character ? this.$store.commit('ADD_TIMELINE', {date: this.character.status, text: "Status"}) : null;
    
    },
    computed:{
      timeline() {
          return this.$store.getters.TIMELINE;
      },
    }
  };
</script>