<template>
    <v-container>
        <h1 align='center'>Occupation</h1>
        <table>
            <tr>
              <th colspan="2">Occupation</th>
              <th>Character</th>
            </tr>
            <tr v-for="(value, propertyName, index) in Occupation">
              <td>{{ propertyName }} </td>
              <td></td>
              <td><p v-for="val in value">{{val.name}}</p></td>
            </tr>
        </table>
    </v-container>
</template>

<script>
  import axios from "axios";

  export default {
    name: 'occupation',
    data: function() {
      return {
        headers: [
            {
            text: 'Occupation',
            align: 'start',
            sortable: false,
            value: 'occupation',
            },
            { text: 'Name', value: 'name' },
        ],
      };
    },
    mounted() {
      this.$store.commit('SET_CURRENT_VIEW', 'occupation');
      this.$store.dispatch('GET_OCCUPATION', '');
    },
    computed: {
      Occupation(){
        return this.$store.getters.CH_OCCUPATION;
      }
    }
  };
</script>

<style scoped>
      table {
        border-spacing: 0 10px;
        font-family: 'Open Sans', sans-serif;
        font-weight: bold;
        width: 100%;
      }
      th {
        padding: 10px 20px;
        background: #56433D;
        color: #F9C941;
        border-right: 2px solid; 
        font-size: 0.9em;
      }
      th:first-child {
        text-align: center;
      }
      th:last-child {
        border-right: none;
      }
      td {
        vertical-align: middle;
        padding: 10px;
        font-size: 14px;
        text-align: center;
        border-top: 2px solid #56433D;
        border-bottom: 2px solid #56433D;
        border-right: 2px solid #56433D;
      }
      td:first-child {
        border-left: 2px solid #56433D;
        border-right: none;
      }
</style>

