<template>
  <v-container>
      <v-layout wrap>
      <v-flex xs12 sm12 md12 v-for="(character, index) in character" :key="`${character.name}-${index}`">
        <v-card
          class="vv-card"
          hover
        >
        <div class="test">
            <v-img class="white--text vv-card-image grey lighten-2" height="600px" :src="character.img">
              <v-layout fill-height align-center justify-center ma-0>
                <v-flex xs12 d-flex align-center justify-center class="episod-name">
                  <div>
                    <h3 class="headline">{{character.name}}</h3>
                    <p class="title">{{character.nickname}}</p>
                  </div>
                </v-flex>
              </v-layout>
            </v-img>
          </div>
        </v-card>
      </v-flex>
    </v-layout>
      <div v-for="aa in quotes_characters">
          Season {{aa.quote}} Episode {{aa.author}}
      </div>
        <time-line :character="character[0]" :episode="characters_in_episode"></time-line>
        <twitter-button
          :url="url"
          btnText="Share"
          title="Person"
          description="I like this person!"
        />
    </v-container>
</template>

<script>
    import axios from "axios";
    import TimeLine from "../views/TimeLine.vue";
    import TwitterButton from "vue-share-buttons/src/components/TwitterButton";

export default {
    name: 'character',
    components: {
        timeLine: TimeLine,
        TwitterButton,
    },
    data: function() {
      return {
      };
    },
    methods: {
       setView: function(view){
          this.$store.commit('SET_CURRENT_VIEW', view)
      },
    },
    mounted() {
      this.$store.commit('SET_CURRENT_VIEW', 'character');
      this.$store.dispatch('GET_CHARACTER_DETAILS_BY_ID', this.$route.params.ch_id);
      this.$store.dispatch('GET_RESPONSIBLE_DEATH', this.$route.params.ch_name);
      this.$store.dispatch('GET_CHARACTER_IN_EPISODE', this.$route.params.ch_name);
      this.$store.dispatch('GET_QUOTES', this.$route.params.ch_name);
      this.$store.dispatch('GET_URL', window.location.href);
    },
    computed:{
      character() {
          return this.$store.getters.CHARACTER;
      },
      characters_in_episode(){
          return this.$store.getters.EPISODES;
      },
      responsible_deaths(){
          return this.$store.getters.CH_DEATHS;
      },
      quotes_characters(){
          return this.$store.getters.CH_QUOTES;
      },
      url(){
         return this.$store.getters.URL;
      }
    }
  };
</script>

<style scoped lang="scss">
.vv-card {
  .v-image {
    transition: all 1s ease;
    -webkit-filter: blur(5px);
    z-index: 1;
  }
  .v-image {
    &:after {
      position: absolute;
      content: "";
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.6);
      opacity: 1;
      transition: 0.3s opacity;
    }
  }
  &:hover {
    &:before {
      border: 15px solid yellow;
      opacity: 0.4;
    }
  }
  .headline,
  .subheading {
    width: 100%;
  }
  .episod-name {
    z-index: 2;
    flex-wrap: wrap;
    text-align: center;
    p {
      margin: 0;
    }
  }
  .list {
    height: 110px;
    overflow-y: scroll;
  }
  .border {
    list-style: none;
    position: relative;
    padding: 0;
  }
  .border li {
    font-family: "Trebuchet MS", "Lucida Sans";
    padding: 7px 20px;
    z-index: 3;
    margin-bottom: 10px;
    border-radius: 5px;
    border-left: 10px solid #f05d22; 
    box-shadow: 2px -2px 5px 0 rgba(0,0,0,.1),
        -2px -2px 5px 0 rgba(0,0,0,.1),
        2px 2px 5px 0 rgba(0,0,0,.1),
        -2px 2px 5px 0 rgba(0,0,0,.1);
    font-size: 20px;
    letter-spacing: 2px;
    transition: 0.3s all linear;
  }
  .favourite {
    width: 30px;
    height: 30px;
    color: red;
  }
  .material-icons{
    color: red;
  }
  .favourite_button{
    background: "yellow"
  }

  .fav {
  color: rgb(170, 170, 170);
  fill: currentColor;
  width: 20px !important;
  height: 20px !important;
  &:hover {
    cursor: pointer;
    color: #db1d40;
  }
}
.liked {
  fill: currentColor;
  color: #db1d40 !important;
}
}
</style>
