<template>
  <v-container>
      <h1 align='center'>Winners</h1>
        <table>
            <tr>
              <th colspan="2">Character:</th>
              <th>Number of kills: </th>
              <th>Killed: </th>
            </tr>
            <tr v-for="(value,index) in Winners">
              <td></td>
              <td :class="'person-' + index">
                <span class='index'>
                    {{index + 1}}
                </span>
                  {{value[0]}} 
              </td>
              <td>{{value[1].length}}</td>
              <td>
                <p v-for="killed in value[1]">
                    {{killed.death}}
                </p>
              </td>
            </tr>
        </table>
  </v-container>
</template>

<script>
  import axios from "axios";

  export default {
    name: "winners",
    props: ['person', 'death', 'episode'],
    data: function () {
      return {};
    },
    mounted() {
        this.$store.commit('SET_CURRENT_VIEW', 'winners');
        this.$store.dispatch('GET_WINNERS', '');
    },
    computed:{
        Winners(){
            return this.$store.getters.WINNERS.flat();
        }
    }
  };
</script>

<style>
      table {
        border-spacing: 0 10px;
        font-family: 'Open Sans', sans-serif;
        font-weight: bold;
        width: 100%;
      }
      th {
        padding: 10px 20px;
        background: #56433D;
        color: #F9C941;
        border-right: 2px solid; 
        font-size: 0.9em;
      }
      th:first-child {
        text-align: center;
      }
      th:last-child {
        border-right: none;
      }
      td {
        vertical-align: middle;
        padding: 10px;
        font-size: 14px;
        text-align: center;
        border-top: 2px solid #56433D;
        border-bottom: 2px solid #56433D;
        border-right: 2px solid #56433D;
      }
      td:first-child {
        border-left: 2px solid #56433D;
        border-right: none;
      }
      .person-0{
        color: #FFD700;
        font-weight: bold;
        font-size: 20px;
      }
      .person-1{
        font-weight: 900;
        color: #c0c0c0;
        font-size: 16px;
      }
      .person-2{
        font-weight: 800;
        color: #CD7F32;
        font-size: 14px;
      }
      
      .person-3{
        font-weight: 600;
        font-size: 11px;
      }

      .person-4{
        font-weight: 400;
        font-size: 10px;
      }

      .index{
        margin-right: 60px;
      }

</style>